<template>
	<b-dropdown id="localeSwitcher">

		<template slot="button-content">
			<img :src="currentLang.flag" width="30" :alt='currentLang.name'> <small>{{currentLang.name}}</small>
		</template>

		<b-dropdown-item
			v-for="(lang, i) in langs"
			:key="`lang-${i}`"
			:value="lang.locale"
			@click="switchLang(lang.locale)"
		>
			<img :src="lang.flag" width="30" alt=""> {{lang.name}}
		</b-dropdown-item>
	</b-dropdown>



</template>
<script>
  export default {
    name: "SelectLocale",
	  data(){
      return{
        langs: [
          {
            locale: 'en',
	          name: 'English',
            flag: 'data:image/png;base64,'
          },
          {
            locale: 'ar',
	          name: 'العربية',
            flag: 'data:image/png;base64,'
          },
        ]
      }
	  },
	  methods:{
      switchLang(locale){
        if (this.$i18n.locale != locale){
	        // change language in global variable
	        this.$i18n.locale = locale

		      // change router parameter
		      this.$router.push({
			      params:{ lang: locale }
		      })
        }
      }
	  },
	  computed:{
      currentLang(){
        let result = this.langs.find(lang => {
          return lang.locale === this.$i18n.locale
        })
        return result
      }
	  }
  }
</script>

<style lang="scss">
#localeSwitcher{
	min-width: 70px;


	.dropdown-menu{
		border-radius: 20px
	}
}


</style>